@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@500&family=Roboto:wght@100&display=swap");

html,
body {
  color: #373737;
}

.cont a{
  text-decoration: underline;
  color: #856084;
}
.cont a:hover{
  text-decoration: underline;
  color: #614660;
  cursor: pointer;
}

#backH{

    text-align: center;
  }



.header1 {
  margin-top: 5%;
  text-align: center;
  padding: 2%;
  font-size: 2.5vw;
}

.header2 {
  text-align: center;
  background-color: #a7b4b4;
  padding: 2%;
  border-radius: 5em;
  font-size: 1.7vw;
  color: #ffecec;
}
.pro {
  padding-top: 1%;
}
.cont {
  color: #292929;
  padding-top: 1%;
  width: 70%;
  margin: auto;
}

#yarntrio {
  display: flex;
  justify-content: center;
  border: none;
  background-color: transparent;
}
#yarntrio img {
  width: 15%;
  padding: 2%;
}
.cont p {
  text-align: center;
  line-height: 1.4;

}

#findfunimg{
  display: flex;
  justify-content: center;
  border: none;
  background-color: transparent;
}

#findfunimg img{
  width: 40%;
}

#greenlifeimg{
  display: flex;
  justify-content: center;
  border: none;
  background-color: transparent;
}
#greenlifeimg img{
  width: 50%;
}

#virustrio{
  display: flex;
  justify-content: center;
      width: 100%;
  border: none;
  background-color: transparent;
  
}

#virus1{
  width: 25%;
  padding: 2%;
}
#virus2{
  width: 15%;
  padding: 2%;
}

#more {
  text-align: right;
  text-decoration: none;
  margin-top: 3%;
  border: none;
  background-color: transparent;
  font-family: 'Montserrat', sans-serif;
}

#more :hover{
  text-decoration: underline;
  cursor: pointer;
}

@media screen and (max-width: 768px) {


  .header1{
margin-top: 10%;
    font-size: 5.5vw;
  }
.header2 {
  text-transform: uppercase;
  font-family: "Josefin Sans", sans-serif;
  text-decoration: none;
  text-align: center;
  padding: 3%;
  border-radius: 5em;
  font-size: 3vw;
}

#yarntrio img {
  width: 20%;
  padding: 2%;
}

#virus1{
  width: 35%;
  padding: 2%;
}
#virus2{
  width: 25%;
  padding: 2%;
}

#findfunimg img{
  width: 60%;
}

}